*,
html {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
@import url("https://fonts.googleapis.com/css2?family=Abel&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Fjalla+One&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Lobster&display=swap");
.navContainer {
  background-color: white;
  display: flex;
  justify-content: space-around;
  align-items: center;
  min-height: 10vh;
}
.navUl {
  font-family: "Lobster", cursive;
  width: 70%;
  display: flex;
  justify-content: space-around;
  align-items: center;
  list-style: none;
}
li {
  color: black;
  font-size: 1.5rem;
}
.navUl > li:hover {
  scale: 1.1;
}
body {
  background-color: rgba(248, 253, 254, 0.409);
}
.link {
  color: white;
  text-decoration: none;
}
.centered {
  width: 100%;
  margin: 0 auto;
}
.logoR {
  font-size: 2.5rem;
  color: rgb(231, 183, 23);
  font-weight: bold;
}
.logoName {
  font-size: 1.7rem;
  color: black;
  font-weight: bold;
  letter-spacing: 3px;
}
.appContainer {
  background-image: linear-gradient(
      to top,
      rgba(2, 0, 0, 0.842) 60%,
      rgba(62, 62, 63, 0.856)
    ),
    url(./images/jason-leung-unsplash.jpg);
  width: 100vw;
  height: 90vh;
  background-size: cover;
  padding-top: 5rem;
  color: white;
  text-transform: uppercase;
  margin: 0 auto;
}
